/* Dashboard Container */
.dashboard-container {
    padding: 30px;
    background-color: #1f1f1f; /* Dark background for the container */
    border-radius: 10px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3); /* Slightly darker shadow for better contrast */
    max-width: 1200px;
    margin: auto;
    position: relative;
}
.hero-section h1 span:nth-child(1) {
    color: #fff;  /* Job text in white */
}

.hero-section h1 span:nth-child(2) {
    color: #1DB954; /* Sprout text in Spotify-like green */
}

/* Hero Section Styling */
.hero-section {
    background-color: #121212; /* Slightly darker for the hero section */
    color: #fff;
    padding: 40px;
    border-radius: 10px;
    text-align: center;
    margin-bottom: 30px;
}

.hero-section h1 {
    font-size: 2.5em;
    font-weight: bold;
    color: #1DB954;
    margin-bottom: 20px;
}

.hero-section p {
    font-style: oblique;
    font-size: 1.2em;
    color: #ecf0f1;
    margin-bottom: 20px;
}

.hero-section button {
    background-color: #1DB954;
    padding: 12px 20px;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.1em;
    transition: all 0.3s ease;
}

.hero-section button:hover {
    background-color: #1ed760;
    transform: scale(1.05);
}

/* Features Section */
.features-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin-top: 40px;
}

.feature-box {
    background-color: #2c2c2c;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
}

.feature-box h3 {
    font-size: 1.5em;
    color: #fff;
    margin-bottom: 15px;
}

.feature-box p {
    color: #aaa;
}

/* Stats Section */
/* Stats Section styling */
.stats-section {
    display: flex;
    justify-content: space-evenly; /* Spreads out the boxes evenly with space in between */
    margin-top: 50px;
    width: 100%; /* Ensure it takes up full width */
    max-width: 1200px; /* Optional: set a max width for larger screens */
}

/* Stats Box Styling */
.stats-box {
    background-color: #2c2c2c;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 200px;
    margin: 0 20px; /* Space between boxes */
}

.stats-box h4 {
    color: #1DB954;
    font-size: 1.6em;
}

.stats-box p {
    color: #fff;
}

/* Responsive Design for Stats Section */
@media (max-width: 768px) {
    .stats-section {
        flex-direction: column; /* Stack items vertically on small screens */
        align-items: center;
    }

    .stats-box {
        margin: 10px 0; /* Space between stacked items */
    }
}


