/* Hero Section with Background Image */
.hero-section {
    position: relative;
    width: 100%;
    padding: 100px 20px;
    text-align: center;
    color: white;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 8px;
}

/* Overlay for readability */
.hero-section::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent overlay */
    z-index: 1;
}

/* Hero content styling */
.hero-section h1,
.hero-section p {
    position: relative;
    z-index: 2;
}

/* Hero Section Button Styling */
.hero-section button {
    position: relative;
    z-index: 2;
    margin-top: 20px;
}
