/* Header Styling */
.header {
    background-color: #121212; 
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1); 
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; 
}

/* Logo styling */
.logo h1 {
    font-size: 2.2em;
    font-weight: bold;
    margin: 0;
}

.logo h1 span:nth-child(1) {
    color: #fff; /* Job text in white */
}

.logo h1 span:nth-child(2) {
    color: #1DB954; /* Sprout text in Spotify-like green */
}

/* Nav styling */
nav ul {
    display: flex;
    align-items: center; /* Center items vertically */
    list-style-type: none;
    margin: 0;
    padding: 0;
    flex-wrap: wrap; /* Allow wrapping on small screens */
}

nav ul li {
    margin: 0 10px;
    position: relative; /* Required for dropdown positioning */
}

/* Nav links styling */
nav ul li a {
    text-decoration: none;
    color: #fff; /* White color for nav links to contrast the black background */
    font-size: 1.1em;
    transition: color 0.5s ease, transform 0.3s ease; /* Slightly slower transition for hover */
}

nav ul li a:hover {
    color: #1ed760; /* Slightly different green on hover */
    transform: scale(1.05); /* Slight scaling effect for links */
}

/* Accessibility: focus state */
nav ul li a:focus {
    outline: 2px solid #1DB954; /* Highlight with Spotify-like green */
}

/* Dropdown menu styling */
.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #1f1f1f; /* Dark background for dropdown */
    border: 1px solid #333; /* Subtle border for dropdown */
    padding: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Shadow for depth */
    display: flex;
    flex-direction: column;
    z-index: 1000;
    min-width: 200px; /* Increased width for better alignment */
}

.dropdown-menu li {
    margin: 5px 0;
    text-align: left; /* Ensures left alignment for text inside the list */
    list-style-type: disc; /* Adds bullet points */
    list-style-position: inside; /* Positions bullets inside the dropdown */
}

.dropdown-menu li a {
    color: #ccc; /* Slightly lighter text color for dropdown links */
    font-size: 0.95em;
    transition: color 0.3s ease, transform 0.3s ease;
    display: inline-block; /* Ensures text is displayed inline */
    text-align: left; /* Explicitly ensures text is left-aligned */
}

.dropdown-menu li a:hover {
    color: #1DB954; /* Spotify-like green on hover */
    transform: scale(1.05); /* Slight scaling effect */
}

/* Login and Signup button styling */
.nav-button {
    padding: 0.5em 1em;
    background-color: #1DB954;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1.1em;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.nav-button:hover {
    background-color: #1ed760; /* Change color on hover for effect */
    transform: scale(1.05); /* Slight scaling effect for button hover */
}

/* Responsive Design */
@media (max-width: 768px) {
    .logo h1 {
        font-size: 1.8em; /* Smaller logo text on small screens */
    }
    
    nav ul {
        justify-content: center; /* Center nav items on small screens */
    }

    nav ul li {
        margin: 5px 15px; /* Adjust spacing for smaller screens */
    }

    nav ul li a {
        font-size: 1em; /* Adjust font size for readability */
    }

    .nav-button {
        font-size: 1em; /* Adjust button font size on small screens */
        padding: 0.4em 0.8em;
    }
}
